@import "../../mixins";
.board-card {
    width: 280px;
    height: 100%;
    margin: 0 4px;
    padding: 10px 0;
    > :first-child {
        width: 100%;
        max-height: 80vh;
        padding: 5px;
        background-color: #ebecf0;
        border-radius: 3px;
        border: 0.5px solid rgba(128, 128, 128, 0.5);
        justify-content: center;
        flex-direction: column;
        overflow: hidden;
        white-space: nowrap;
        > :first-child {
            margin-bottom: 5px;
        }
        input {
            font-size: 15px;
            font-weight: 600;
            border: none;
            cursor: pointer;
            background-color: #ebecf0;
            &::placeholder {
                color: black;
                font-size: 1em;
            }
            &:focus-visible {
                outline: solid 2px #0079bf;
                cursor: text;
                background-color: #fff;
            }
        }
        ul {
            overflow: auto;
            margin-block-start: 0;
            margin-block-end: 0;
            li {
                list-style: none;
                background-color: #f4f5f7;
                margin: 10px 0;
                height: 100%;
                border-radius: 5px;
                padding: 7px;
                cursor: pointer;
                box-shadow: 0 1px 0 0 rgba(128, 128, 128, 0.5);
                transition: 0.3s;
                font-size: 14px;
                > span {
                    white-space: break-spaces;
                }
                &:hover {
                    background-color: #dbdcdd;
                }
            }
        }
    }
    .label-color-to-preview-container {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        width: 100%;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
        transition: 0.3s;
        .label-color-to-preview {
            width: 40px;
            height: 8px;
            border-radius: 4px;
            margin: 0 4px 4px 0;
            cursor: pointer;
            transition: 0.3s;
        }
        .label-color-open-to-preview {
            width: auto;
            height: auto;
            padding: 3px 5px;
            line-height: 16px;
            max-width: 198px;
            color: white;
            border-radius: 4px;
            font-weight: 500;
            margin: 0 4px 4px 0;
            cursor: pointer;
            font-size: 12px;
            overflow: hidden;
            transition: 1s;
        }
    }
    .task-cover-color {
        width: 100%;
        height: 32px;
        border-radius: 3px 3px 0 0;
        margin-bottom: 5px;
        position: relative;
        right: 7px;
        width: 105.5%;
        top: 0;
        transform: translateY(-7px);
    }
    .task-cover-img {
        width: 100%;
        height: 130px;
        border-radius: 3px 3px 0 0;
        margin-bottom: 5px;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        position: relative;
        right: 7px;
        width: 105.5%;
        top: 0;
        transform: translateY(-7px);
    }
    .container-of-all {
        flex-direction: row;
        flex-wrap: wrap;
        display: flex;
        justify-content: space-between;
        .buttom-preview-info {
            flex-direction: row;
            flex-wrap: wrap;
            display: flex;
            align-items: center;
            color: #8b95a7;
            > * {
                margin: 2px 5px;
            }
            .due-date-to-preview {
                border-radius: 3px;
                padding: 2px 3px;
                width: 80px;
                display: flex;
                justify-content: space-around;
                align-items: center;
                .icon {
                    font-size: 15px;
                }
                .font-awesome-clock {
                    display: inline-block;
                }
                .font-awesome-home {
                    display: none;
                }
                .font-awesome-check-square {
                    display: none;
                }
                &:hover {
                    background-color: #eb5a46 !important;
                    .font-awesome-clock {
                        display: none;
                    }
                    .font-awesome-home {
                        display: inline-block;
                    }
                    .font-awesome-check-square {
                        display: inline-block;
                    }
                }
            }
        }
        .avatar-card-preview {
            display: flex;
            justify-content: flex-end;
            > * {
                margin-left: -4%;
            }
        }
    }
    .add-task-container {
        padding-top: 5px;
        input,
        textarea {
            background-color: #fff;
            width: 100%;
            width: 100%;
            font-size: 14px;
            font-weight: 400;
            border-radius: 3px;
            color: #172b4d;
            margin-bottom: 10px;
            &:focus-visible {
                outline: solid 2px #0079bf;
                cursor: text;
            }
        }
    }
}

.card-modal {
    position: relative;
    left: -5px;
    width: 280px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    overflow: hidden;
    white-space: nowrap;
    z-index: 10;
    transition: 0.3s;
    button {
        width: 95%;
        height: 40px;
        &:hover {
            background-color: red;
        }
    }
}

.card-title {
    &:hover {
        cursor: pointer;
    }
}

.title {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.manage-card {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 35px;
    height: 35px;
    cursor: pointer;
    border-radius: 3px;
    padding: 10px;
    font-size: 20px;
    font-weight: 600;
    border-radius: 4px;
    transition: 0.3s;
    &:hover {
        background-color: rgba(9, 30, 66, 0.08);
    }
}

.add-task-btns {
    display: flex;
    align-items: center;
    justify-content: space-between;
    > :nth-child(2) {
        width: 25px;
        height: 25px;
        border-radius: 7px;
    }
}

.add-task-btn {
    height: 32px;
    width: 100%;
    margin-top: 5px;
    border-radius: 5px;
    background-color: rgba(128, 128, 128, 0);
    padding: 7px;
    cursor: pointer;
    text-align: left;
    &:hover {
        filter: brightness(75%);
    }
}

@include normal {
    .board-card {
        > :first-child {
            width: 100%;
        }
    }
}
